import client from './client'

export default {
    fetchOauthRedirectUrl: (provider) => {
        return new Promise((resolve, reject) => {
            client.get(`/oauth/${provider}`)
                .then(res => resolve(res.data))
                .catch(err => {
                    reject(new Error(err.response.data.message || err.message))
                })
        })
    },
    handleCallback: (provider, routeParams) => {
        return new Promise((resolve, reject) => {
            client.get(`/oauth/${provider}/callback`, { params: routeParams })
                .then(res => resolve(res.data))
                .catch(err => {
                    reject(new Error(err.response.data.message || err.message))
                })
        })
    },
}



