<template>
  <header class="header">
    <img class="logo" src="@/assets/images/logo.png" alt="">
    <div class="actions">
      <span v-if="user">ログイン中: {{user.name}}</span>
    </div>
  </header>
</template>

<script>
import UsersApi from '@/api/users'

export default {
  name: 'SampleTemplate',
  data () {
    return {
      user: null
    }
  },
  beforeMount(){
    UsersApi.me().then((user) => {
          this.user = user
      })
      .catch(e => {throw e})
    }
}
</script>

<style scoped>
    .logo {
      height: 40px;
    }
    .header {
      position: relative;
      padding: 1rem;
      background-color: var(--sub-color);
      display: flex;
      justify-content: space-between;
    }
</style>



