<template>
  <div>
    <button @click="login">login</button>
    <span>name {{ name }}</span>
    <span>logedin {{ logedin }}</span>
  </div>
</template>

<script>
  export default {
    name: "Counter",
    computed: {
      name() { return this.$store.getters.user},
      logedin() { return this.$store.getters.isLoggedIn? 't':'f' }
    },
    methods: {
      login() {
        this.$store.dispatch('login', { user: {id:1,name:'hoge'} })
      }
    }
  }
</script>

<style scoped>

</style>


