<template>
    <SingleLayout>
        <Panel type="default">
          <div>
            <div>
              <input type="text" v-model="asciiArt.title"/>
              タイトル, ユーザー（飾り）
            </div>
            <textarea class="ascii-art-editor" v-model="asciiArt.content" />
            <button @click="submit">送信</button>
          </div>
        </Panel>
    </SingleLayout>
</template>

<script>
import SingleLayout from '@/components/templates/SingleLayout'
import AsciiArtApi from '@/api/ascii_art'
import Panel from '@/components/atoms/Panel'

export default {
  name: 'New',
  components: {
    SingleLayout,
    Panel
  },
  data () {
    return {
      asciiArt: {
        title: "aa",
        content: "aaka",
      }
    }
  },
  methods: {
    submit() { 
      const { title, content } = this.asciiArt
      AsciiArtApi.add({ title, content })
      .then((data) => {
        console.log(data)
      })
      .catch(e => {throw e})
    }
  }
}
</script>

<style scoped>
    .ascii-art-editor {
      width: 100%;
      height: 400px;
      font-family: "giko2";
    }
</style>



