<template>
    <div class="column is-6 is-offset-3 is-centered" style="margin-top: 5em;">
        <div class="card">
            <div class="card-content has-text-centered">
                <p class="title is-6">
                    ソーシャルアカウントでログインする
                </p>
            </div>
        </div>
    </div>
</template>

<script>
import AuthApi from '@/api/auth'

export default {
    data() {
        return {
        };
    },

    computed: {
    
    },

    mounted() {
        AuthApi.handleCallback('github', this.$route.query)
        .then((user) => {
            this.$router.push({ name: 'Index' });
        })
        .catch(e => {throw e})
    }
};
</script>



