<template>
    <div class="aa-item">
        <Panel type="default">
            <div class="aa-item-header">
                <span>【{{ascii_art.id}}】</span>
                <h2 class="aa-item-title">{{ascii_art.title}}</h2>
            </div>
            <AAComment :ascii_art="ascii_art" />
        </Panel>
    </div>
</template>

<script>
import Panel from '@/components/atoms/Panel'
import AAComment from '@/components/molecules/AAComment'

export default {
    name: 'ThreadPreview',
    components: {
        Panel,
        AAComment,
    },
    props: {
        ascii_art: {
            type: Object,
            required: true
        }
    }
}
</script>

<style scoped>
    .aa-item-header {
        font-weight: 700;
    }
    .aa-item {
        margin-top: 1rem;
    }
    .aa-item-title {
        color: var(--thread-title-color);
        display: inline;
    }
    .comment-name {
        color: var(--comment-name-color);
    }
</style>



