<template>
    <SingleLayout>
        <ThreadPreview v-for="ascii_art in ascii_art_list" :key="ascii_art.id" :ascii_art="ascii_art"/>
    </SingleLayout>
</template>

<script>
import SingleLayout from '@/components/templates/SingleLayout'
import ThreadPreview from '@/components/molecules/ThreadPreview'
import AsciiArtApi from '@/api/ascii_art'

export default {
  name: 'Index',
  components: {
    SingleLayout,
    ThreadPreview
  },
  data () {
    return {
      ascii_art_list: []
    }
  },
  created: function() {
    return AsciiArtApi.list()
      .then((list) => {
        console.log(list)
        this.ascii_art_list = list
      })
      .catch(e => {throw e})
  }
}
</script>

<style scoped>

</style>



