<template>
    <div class="aa" :class="font">{{ aa }}</div>
</template>

<script>
export default {
    name: 'AAPreview',
    props: {
            aa: {
                type: String,
                default: "",
                required: true,
            },
            font: {
                type: String,
                default: "giko2",
                required: true,
            }
    }
}
</script>

<style scoped>
    .aa {
        padding: 0.5rem;
        white-space: pre;
        font-size: 16px;
        line-height: 18px;
    }
    .aa.giko2 {
        font-family: "giko2";
    }
</style>



