import client from './client'

export default {
    list: () => {
        return new Promise((resolve, reject) => {
            client.get(`/ascii_art`)
                .then(res => resolve(res.data))
                .catch(err => {
                    reject(new Error(err.response.data.message || err.message))
                })
        })
    },
    add: ({ title, content }) => {
        return new Promise((resolve, reject) => {
            client.post(`/ascii_art`, { title, content })
                .then(res => resolve(res.data))
                .catch(err => {
                    reject(new Error(err.response.data.message || err.message))
                })
        })
    },

    update: ({ id, title, content }) => {
        return new Promise((resolve, reject) => {
            client.patch(`/ascii_art/${id}`, { title, content })
                .then(() => resolve())
                .catch(err => {
                    reject(new Error(err.response.data.message || err.message))
                })
        })
    },

    remove: ({ id }) => {
        return new Promise((resolve, reject) => {
            client.delete(`/ascii_art/${id}`)
                .then(() => resolve())
                .catch(err => {
                    reject(new Error(err.response.data.message || err.message))
                })
        })
    },
}



