<template>
    <div class="column is-6 is-offset-3 is-centered" style="margin-top: 5em;">
        <div class="card">
            <div class="card-content has-text-centered">
                <p class="title is-6">
                    ソーシャルアカウントでログインする
                </p>
                <p>リダイレクトしています。。。</p>
            </div>
        </div>
    </div>
</template>

<script>
import AuthApi from '@/api/auth'

export default {
    mounted() {
        AuthApi.fetchOauthRedirectUrl('github')
        .then((data) => {
            console.log(data)
            window.location.href = data.redirectUrl
        })
        .catch(e => {throw e})
    }
};
</script>



