<template>
    <div class="comment">
        <div class="comment-header">
            {{idx}} 名前：<span class="comment-name">{{user.nickname}}</span>  ID: {{user.name}}
        </div>
        <div class="comment-body">
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Comment',
    props: {
        idx: {
            type: Number,
            required: true
        },
        user: {
            type: Object,
            required: true,
        }, 
        time: {
            type: String,
            required: true,
        }
    }
}
</script>

<style scoped>
    .comment-name {
        color: var(--comment-name-color);
    }
</style>



