<template>
    <Comment :idx="1" :user="ascii_art.user" :time="ascii_art.created_at">
        <AAPreview :aa="ascii_art.content" font="giko2"/>
    </Comment>
</template>

<script>
import Comment from '@/components/atoms/Comment'
import AAPreview from '@/components/atoms/AAPreview'

export default {
    name: 'AAComment',
    components: {
        Comment,
        AAPreview
    },
    props: {
        ascii_art: {
            type: Object,
            required: true
        }
    }
}
</script>

<style scoped>

</style>



