import Vue from 'vue'
import Router from 'vue-router'
import Sample from '@/components/pages/Sample'
import Index from '@/components/pages/ascii_art/Index'
import GithubRedirect from '@/components/pages/auth/oauth/GithubRedirect'
import GithubCallback from '@/components/pages/auth/oauth/GithubCallback'
import AsciiArtNew from '@/components/pages/ascii_art/New'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/sample',
      name: 'Sample',
      component: Sample
    },
    {
      path: '/oauth/github',
      name: 'githubRedirect',
      component: GithubRedirect
    },
    {
      path: '/oauth/github/callback',
      name: 'githubCallback',
      component: GithubCallback
    },
    {
      path: '/ascii_art/new',
      name: 'asciiArtNew',
      component: AsciiArtNew
    }
  ]
})



