<template>
  <div class="wrapper" :class="type">
    <div class="inner">
      <slot>content</slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Panel',
  props: {
    type: String
  }
}
</script>

<style scoped>
    .wrapper {
      border: solid 1px;
      padding: 7px;
    }
    .inner {
      border: solid 1px;
      padding: 3px;
    }

    /* <Panel type="default"> */
    .wrapper.default {
      background-color: var(--default-bg-color);
      color: var(--default-tx-color);
      border-color: var(--default-ol-l-color) var(--default-ol-d-color) var(--default-ol-d-color) var(--default-ol-l-color);
    }
    .inner.default {
      border-color: var(--default-ol-d-color) var(--default-ol-l-color) var(--default-ol-l-color) var(--default-ol-d-color);
    }

    /* <Panel type="info"> */
    .wrapper.info {
      background-color: var(--info-bg-color);
      color: var(--info-tx-color);
      border-color: var(--info-ol-l-color) var(--info-ol-d-color) var(--info-ol-d-color) var(--info-ol-l-color);
    }
    .inner.default {
      border-color: var(--info-ol-d-color) var(--info-ol-l-color) var(--info-ol-l-color) var(--info-ol-d-color);
    }
</style>



